export function AddMarketPageContent() {

    // const [ui, setUI] = useState(null)
    // useEffect(() => {
    //     setTimeout(() => {
    //         setUI(test().components.UI())
    //     }, 1000 * 2)
    // }, [])
    // url = 'http://192.168.177.180:3000/resources/markets/market1.iife.js'
    const [url, setUrl] = useState<string>('')
    const [urlIsWrong, setUrlIsWrong] = useState(url ? !isUrl(url) : false);
    const [urlFetchError, setUrlFetchError] = useState<string | undefined>(undefined)
    const [marketLib, setMarketLib] = useState<IMarketLib | null>(null)
    const { t } = useTranslation('homepage/components/markets/add-market-page-content/content')
    const [isFetchingUrl, fetchUrlTransition] = useTransition()
    function onUrlChange(url: string) {
        setUrlFetchError(undefined)
        console.log('value is', url)
        setUrl(url)
        const urlIsOk = isUrl(url);
        console.log('urlIsOk=', urlIsOk)
        setUrlIsWrong(!urlIsOk && !!url)
    }
    useEffect(() => {
        if (urlIsWrong || !url) return;
        fetchUrlTransition(fetchUrl2)
    }, [url, urlIsWrong])
    async function fetchUrl() {
        console.log('fetchUrl', url)
        if (!url) {
            return;
        }
        const [err, res] = await to(axios.get(url))
        if (err) {
            setUrlFetchError(err.message)
            return
        }
        const scriptString = res?.data
        const info = marketUtils.parseMarketInfoByString(scriptString)
        console.log('info222=', info)
        if (!info) return
        setMarketLib(info)
        // const ui = info.marketData.MarketSummaryUI()
        // console.log('uuuuuuuuuuuuui', ui)
        // if(ui)setUI(ui as any)
        return
    }

    async function fetchUrl2(){
        if (!url) {
            return;
        }
        console.log('url=', url)
        const mod = await import(/* webpackIgnore: true */url)
        // const ui = mod.marketData.MarketSummaryUI()
        console.log('mod=', mod, Object.keys(mod) );
        console.log('mod."marketData"=', mod.marketData, Object.keys(mod.marketData) );
        // setUI(mod.marketData.MarketSummaryUI)
        // const container = document.getElementById("remote-root")!;
        // const root = createRoot(container);
        // root.render(createElement(mod.marketData.MarketSummaryUI));
        // setUI(ui)
    }
    useEffect(()=>{
        if(!marketLib)return;
        const ui = marketLib.marketData.MarketSummaryUI()
        console.log('uuuuuuuuuuuuui', ui)
        // if(ui)setUI(ui as any)
        return
    }, [marketLib])

    
    return (
        <Box sx={{ width: "100%" }}>
      <div id="remote-root" />
            <Stack direction={"column"} spacing={2}>
                <FormControl error={urlIsWrong} variant="standard">
                    <InputLabel htmlFor="component-error">{t("AddMarketPageContent.url")} </InputLabel>
                    <Input
                        id="component-error"
                        value={url}
                        onChange={e => onUrlChange(e.currentTarget.value)}
                        aria-describedby="component-error-text"
                    />
                    {urlIsWrong && <FormHelperText id="component-error-text">{t("AddMarketPageContent.urlIsNotValid")}</FormHelperText>}
                </FormControl>
                {urlFetchError && <Box>{urlFetchError}</Box>}
                <Box>
                    <span>
                        <span>
                            {t("AddMarketPageContent.summary")}
                        </span>
                    </span>
                </Box>
                {marketLib && <Box>
                </Box>}
                <Box>
                    {/* {ui} */}
                </Box>
            </Stack>
        </Box>
    )
}