import {IMarketData, IMarketLib} from '@market/lib'
function parseMarketInfoByString(s:string){
    try{
        const lib = new Function(`${s};return lib;`)() as IMarketLib
        if(!lib.marketData){
            throw new Error("no marketData found")
        }
        // const marketData = lib.marketData;
        console.log('info=', lib)
        return lib;
    }catch(e:any){
        console.log('e=', e)
        return null
    }
}
export const marketUtils = {
    parseMarketInfoByString
}