import { AppbarContainer, AppShell } from "@app/ui";
import { Dispatcher, useTranslation } from "@fanfanlo";
import { AddMarketPageContent } from "@src/components/markets/add-market-page-content/AddMarketPageContent";


export default function Index(){
    const {t} = useTranslation('homepage/pages/add-market/content');
    const dispatcher = new Dispatcher()
    return (
        <AppShell>
            <AppbarContainer appbarProps={{title:t("content.title"), back:true, backDispatcher:dispatcher}}>
                <AddMarketPageContent />
            </AppbarContainer>
        </AppShell>
    )
}